
















































import Logo from "../misc/Logo.vue";
import NumberCard from "../home/NumberCard.vue";
import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import { jobsModule, routerModule } from "@/store/modules";
import DoesNotExistScreen from "@/components/misc/DoesNotExistScreen.vue";

@Component({
  components: {
    NumberCard,
    DoesNotExistScreen
  }
})
export default class RepositoryOverviewComponent extends Mixins() {
  get jobId(): string {
    return routerModule.params.jobId;
  }

  get jobData() {
    return jobsModule.getById(this.jobId);
  }

  get details() {
    if (!this.jobData) return [];
    return [
      { title: this.$t("repository.details.name"), value: this.jobData.name }
    ];
  }
}
