<template>
  <fullscreen-loader :value="currentPercentage" v-if="loading"/>
  <div v-else class="ma-4">
    <v-list>
      <v-list-tile v-for="job in jobs" :key="job.id" :to="`/jobs/${job.id}`" avatar>
        <v-list-tile-content>
          <v-list-tile-title>{{job.name}}</v-list-tile-title>
          <v-list-tile-sub-title>Repeats every ...</v-list-tile-sub-title>
        </v-list-tile-content>
      </v-list-tile>
    </v-list>
    <v-btn color="accent" fab fixed bottom right to="/jobs/new">
      <v-icon>mdi-plus</v-icon>
    </v-btn>
  </div>
</template>

<script>
import FullscreenLoader from "../components/misc/FullscreenLoader";
import { mapMutations, mapGetters } from "vuex";

export default {
  components: {
    FullscreenLoader
  },
  data() {
    return {
      currentPercentage: 0
    };
  },
  computed: {
    loading() {
      return this.currentPercentage < 1;
    },
    ...mapGetters({
      jobs: "jobs/allJobs"
    })
  },
  mounted() {
    this.updateLoading();
  },
  methods: {
    updateLoading() {
      setTimeout(() => {
        this.currentPercentage += Math.random() * 0.3;
        if (this.loading) this.updateLoading();
      }, Math.random() * 200);
    }
  }
};
</script>
